import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router";
import PropTypes from "prop-types";
import clsx from "clsx";
import * as Yup from "yup";
import { Formik } from "formik";
import { useSnackbar } from "notistack";
import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  Checkbox,
  Divider,
  FormControlLabel,
  FormHelperText,
  Grid,
  Paper,
  TextField,
  Typography,
  makeStyles,
} from "@material-ui/core";
import QuillEditor from "src/components/QuillEditor";
import DraftEditor from "src/components/DraftEditor";
import DOMPurify from "dompurify";
import FilesDropzone from "src/components/FilesDropzone";
import { EditorState, convertToRaw } from "draft-js";
import { Editor } from "react-draft-wysiwyg";
import draftToHtml from "draftjs-to-html";
import htmlToDraft from "html-to-draftjs";
import { createBlogRequest } from "../../../actions/blogActions";
import convertPathSeo from "src/utils/convertPathSeo";
import ImageDropzone from "src/components/ImageDropzone";

const categories = [
  {
    id: "shirts",
    name: "Shirts",
  },
  {
    id: "phones",
    name: "Phones",
  },
  {
    id: "cars",
    name: "Cars",
  },
];

const useStyles = makeStyles(() => ({
  root: {},
  editor: {
    "& .ql-editor": {
      height: 400,
    },
  },
}));

function ProductCreateForm({ className, ...rest }) {
  const dispatch = useDispatch();
  const [body, setBody] = useState("");
  const [thumbnail, setThumbnail] = useState("");
  const [editorState, setEditorState] = useState(() =>
    EditorState.createEmpty()
  );
  const [isOpen, setIsOpen] = useState(true);

  const handleEditorChange = (state) => {
    setEditorState(state);
    setBody(draftToHtml(convertToRaw(state.getCurrentContent())));
  };
  const createMarkup = (html) => {
    return {
      __html: DOMPurify.sanitizae(html),
    };
  };
  const classes = useStyles();
  const history = useHistory();
  const { enqueueSnackbar } = useSnackbar();

  return (
    <Formik
      initialValues={{
        category: "",
        description: "",
        images: [],
        includesTaxes: false,
        isTaxable: false,
        name: "",
        price: "",
        productCode: "",
        productSku: "",
        salePrice: "",
        description: "",
        source: "",
      }}
      validationSchema={Yup.object().shape({
        // category: Yup.string().max(255),
        // description: Yup.string().max(5000),
        // images: Yup.array(),
        // includesTaxes: Yup.bool().required(),
        // isTaxable: Yup.bool().required(),
        name: Yup.string()
          .max(255)
          .required(),
        // price: Yup.number()
        //   .min(0)
        //   .required(),
        // productCode: Yup.string().max(255),
        // productSku: Yup.string().max(255),
        // salePrice: Yup.number().min(0),
      })}
      onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
        try {
          // Do api call
          var newBody = body
            .replaceAll("height: auto", "height: 260px")
            .replaceAll("height: undefined", "height: 260px")
            .replaceAll("width: undefined", "width: 576px;object-fit: cover;")
            .replaceAll("width: auto", "width: 576px;object-fit: cover;");
          var objet = {
            pathSEO: convertPathSeo(values.name),
            title: values.name,
            body: newBody,
            thumbnail: thumbnail,
            description: values.description,
            source: values.source,
            highlight: isOpen ? 1 : 0,
          };
          console.log(objet);
          await dispatch(
            createBlogRequest(
              objet,
              (res) => {
                console.log("createBlogRequest", res);
                setStatus({ success: true });
                setSubmitting(false);
                enqueueSnackbar("Product Created", {
                  variant: "success",
                });
                history.push("/app/management/products");
              },
              (error) => {
                console.log("errorerror", error);
              }
            )
          );
        } catch (err) {
          setErrors({ submit: err.message });
          setStatus({ success: false });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        setFieldValue,
        touched,
        values,
      }) => (
        <form
          onSubmit={handleSubmit}
          className={clsx(classes.root, className)}
          {...rest}
        >
          <Grid container spacing={3}>
            <Grid item xs={12} lg={12}>
              <Card>
                <CardContent>
                  <Grid
                    className={clsx(classes.root, className)}
                    container
                    justify="space-between"
                    spacing={3}
                  >
                    <Grid item xs={12} lg={6}>
                      <TextField
                        error={Boolean(touched.name && errors.name)}
                        fullWidth
                        helperText={touched.name && errors.name}
                        label="Tên bài viết"
                        name="name"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.name}
                        variant="outlined"
                      />
                      <TextField
                        error={Boolean(
                          touched.description && errors.description
                        )}
                        fullWidth
                        helperText={touched.description && errors.description}
                        label="Giới thiệu ngắn gọn"
                        margin="normal"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        type="text"
                        value={values.description}
                        variant="outlined"
                        style={{ marginTop: 30 }}
                        multiline
                        rows={5}
                        name="description"
                      />
                      <TextField
                        style={{ marginTop: 20 }}
                        error={Boolean(touched.source && errors.source)}
                        fullWidth
                        helperText={touched.source && errors.source}
                        label="Nguồn"
                        name="source"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.source}
                        variant="outlined"
                      />
                      <div style={{ display: "flex" }}>
                        <FormControlLabel
                          control={
                            <Checkbox
                              checked={isOpen}
                              onChange={() => {
                                setIsOpen(!isOpen);
                              }}
                              name="inStock"
                            />
                          }
                          label="Dự án nổi bật"
                        />
                      </div>
                    </Grid>
                    <Grid item xs={12} lg={6}>
                      <Box>
                        <Card>
                          <CardHeader title="Tải ảnh đại diện" />
                          <Divider />
                          {thumbnail !== "" ? (
                            <CardContent>
                              <div
                                style={{
                                  display: "flex",
                                  flexDirection: "column",
                                  alignItems: "center",
                                }}
                              >
                                <img
                                  style={{
                                    width: 300,
                                    height: 200,
                                    objectFit: "cover",
                                  }}
                                  src={thumbnail}
                                />
                                <Box mt={1}>
                                  <Button
                                    color="secondary"
                                    size="large"
                                    variant="contained"
                                    onClick={() => {
                                      setThumbnail("");
                                    }}
                                  >
                                    Xoá
                                  </Button>
                                </Box>
                              </div>
                            </CardContent>
                          ) : (
                            <CardContent>
                              <ImageDropzone setThumbnail={setThumbnail} />
                            </CardContent>
                          )}
                        </Card>
                      </Box>
                    </Grid>
                  </Grid>

                  <Box mt={3} mb={1}>
                    <Typography variant="subtitle2" color="textSecondary">
                      Nội dung
                    </Typography>
                  </Box>
                  <Paper
                    style={{ minHeight: 500, padding: 20 }}
                    variant="outlined"
                  >
                    {/* <QuillEditor
                      className={classes.editor}
                      value={values.description}
                      onChange={(value) => setFieldValue("description", value)}
                    /> */}
                    <DraftEditor
                      style={{ minHeight: 500 }}
                      editorState={editorState}
                      onEditorStateChange={handleEditorChange}
                    />
                  </Paper>
                  {touched.description && errors.description && (
                    <Box mt={2}>
                      <FormHelperText error>
                        {errors.description}
                      </FormHelperText>
                    </Box>
                  )}
                </CardContent>
              </Card>
            </Grid>
          </Grid>
          <Box mt={2}>
            <Button
              color="secondary"
              variant="contained"
              type="submit"
              disabled={isSubmitting}
            >
              Thêm mới bài viết
            </Button>
          </Box>
        </form>
      )}
    </Formik>
  );
}

ProductCreateForm.propTypes = {
  className: PropTypes.string,
};

export default ProductCreateForm;
